import React from 'react';
import {Route} from 'react-router-dom';
import {AvailableLender, Lending} from '../views';

const paths = ['/', '/lending-app', '/lending'];

const SubRoutes = (route) => {
  return (
      route.path !== undefined ?
          <Route
              path={[route.path+route.suffix, route.path]}
              render={props => (
                  <route.component {...props} routes={route.routes}/>
              )}
          /> :
          <Route
              render={props => (<route.component {...props} routes={route.routes}/>)}
          />
  );
};

const routes = [
  {
    path: [paths[0], paths[1]],
    exact: true,
    component: AvailableLender,
    suffix: '.html'
  },
  {
    path: paths[2],
    exact: true,
    component: Lending,
    suffix: '.html'
  }
];

export {paths, routes, SubRoutes};