import React, { Component } from 'react';
import {connect} from 'react-redux';
import TermCondition from './TermCondition';
import RequestInfo from './RequestInfo';
import LendingForm from './LendingForm';
import {CHECK_FIRST_TIME_LOAN, FETCH_USER_INFO, VERIFY_MERCHANT_INFO} from '../reducers/actions/type';
import {postExecuted, checkFirstTimeLoanFlow} from '../reducers/actions/action';
import {withStyles} from "@material-ui/core";
import {pathCheckFirstTimeLoan} from "../utils/Constant";
import requestPayment from "../utils/H5Payment";
import axios from "axios";
const cancelToken = axios.CancelToken.source();

class Lending extends Component {
  constructor(props) {
    super(props);
    this.nextPage = this.nextPage.bind(this);
    this.handleTermConditionAgreement = this.handleTermConditionAgreement.bind(this);
    this.handleApplyLoan = this.handleApplyLoan.bind(this);
    this.state = {
      page: 1
    }
  }

  nextPage() {
    this.setState({ page: this.state.page + 1 })
  }

  handleTermConditionAgreement = (values) => {
    this.props.onCheckFirstTimeLoan(pathCheckFirstTimeLoan, {account_number: "01044000"}, cancelToken.token);
    this.setState({page: 2})
  };

  handleApplyLoan = (values) => {
    requestPayment("123456", "Amret Loan");


    //console.log(values);
  };

  static getDerivedStateFromProps(nextProps, prevState) {
    /*if (nextProps.firstTimeLoan !== undefined && nextProps.firstTimeLoan.first_time !== undefined) {
      console.log("firstTimeLoan");
      console.log(nextProps.firstTimeLoan);
      if (nextProps.firstTimeLoan.first_time === "Yes") {
        //push get user info
        nextProps.onFetchUserInfo(USER_INFO,
            {accountNumber: "01044000", merchantAccount: ""} //01044000 //00049019
            ,
            cancelToken.token);
        console.log(nextProps.firstTimeLoan);
        nextProps.history.push('/lending');
        return ({open: false});
      } else if (nextProps.firstTimeLoan.first_time === "No") {
        //push check kyc updated
        nextProps.onCheckKYCUpdated(CHECK_UPDATE_KYC,
            {accountNumber: "01044000", merchantAccount: ""} //01044000 //00049019
            ,
            cancelToken.token);
      } else {
        return ({open: true});
      }
    }

    //
    if (nextProps.userInfo !== undefined && nextProps.userInfo !== null && nextProps.userInfo.length > 0) {
      console.log("userInfo");
      console.log(nextProps.userInfo);
      return ({page: 2});
    }

    //
    if (nextProps.kycUpdated !== undefined && nextProps.kycUpdated !== null && nextProps.kycUpdated.length > 0) {
      console.log("kycUpdated");
      console.log(nextProps.kycUpdated);
      return ({page: 3});
    }*/

    if (nextProps.users !== undefined && nextProps.users !== null && nextProps.users.length > 0) {
      console.log("User ====================> ");
      console.log(nextProps.user);
      return ({page: 2});
    }

    if(nextProps.kycUpdated !== undefined && nextProps.kycUpdated !== null && nextProps.kycUpdated.length > 0) {
      console.log("kycUpdated ====================> ");
      console.log(nextProps.kycUpdated);
      return ({page: 3});
    }

    return ({page: prevState.page})

  }


  componentWillUnmount() {
    cancelToken.cancel("Api is being canceled");
  }


  render() {
    const { onSubmit, firstTimeLoan, userInfo,  kycUpdated} = this.props;
    const { page } = this.state;
    if(firstTimeLoan !== undefined && firstTimeLoan !== null && firstTimeLoan.length > 0) {
      console.log(firstTimeLoan);
    }
    if(userInfo !== undefined && userInfo !== null && userInfo.length > 0) {
      console.log(userInfo);
    }
    if (kycUpdated !== undefined && kycUpdated !== null && kycUpdated.length > 0) {
      console.log(kycUpdated);
    }
    return (
        <div>
          {page === 1 && <TermCondition onSubmit={this.handleTermConditionAgreement} />}
          {page === 2 && (
              <RequestInfo
                  onSubmit={this.nextPage}
              />
          )}
          {page === 3 && (
              <LendingForm onSubmit={this.handleApplyLoan} />
          )}
        </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    firstTimeLoan: state.firstTimeLoan.data,
    users: state.userInfo.data,
    kycUpdated: state.kycUpdated.data
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onCheckFirstTimeLoan: (url, data, cancelToken) => {
      dispatch(checkFirstTimeLoanFlow(CHECK_FIRST_TIME_LOAN, url, data, cancelToken));
    },
    onFetchUserInfo: (url, data, cancelToken) => {
      dispatch(postExecuted(FETCH_USER_INFO, url, data, cancelToken));
    },
    onCheckKYCUpdated: (url, data, cancelToken) => {
      dispatch(postExecuted(VERIFY_MERCHANT_INFO, url, data, cancelToken));
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Lending);