import React from "react";
import PropTypes from "prop-types/prop-types";
import TextField from "@material-ui/core/TextField";
import IconButton from '@material-ui/core/IconButton';
import InputAdornment from '@material-ui/core/InputAdornment';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import withStyles from "@material-ui/core/styles/withStyles";
import classNames from "classnames";
import TextFieldStyle from "../../assets/jss/material-dashboard-react/components/TextFieldStyle";

const PasswordFieldInput = ({...props}) => {
  const {
    classes,
    className,
    id,
    label,
    input,
    variant,
    showPassword,
    handleClickShowPassword,
    meta: {touched, invalid, error},
    ...custom
  } = props;

  const root = classNames({
    [classes.root]: true,
    [classes.success]: !invalid && !error,
    [className]: className !== undefined
  });

  return (
      <TextField
          className={root}
          id={id}
          variant={variant}
          fullWidth
          type={showPassword ? "text" : "password"}
          label={label}
          placeholder={label}
          error={touched && invalid}
          helperText={touched && error}
          {...input}
          {...custom}
          InputProps={{
            endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                      edge="end"
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                  >
                    {showPassword ? <VisibilityOff/> : <Visibility/>}
                  </IconButton>
                </InputAdornment>
            ),
          }}
      />
  )
};

PasswordFieldInput.propTypes = {
  classes: PropTypes.object,
  className: PropTypes.object,
  id: PropTypes.string,
  label: PropTypes.string,
  input: PropTypes.object,
  variant: PropTypes.oneOf(['standard','outlined','filled']),
  showPassword: PropTypes.bool,
  handleClickShowPassword: PropTypes.func
};

export default withStyles(TextFieldStyle)(PasswordFieldInput);