import {defaultFont, dangerColor} from "../../material-dashboard-react";

const CheckBoxStyle = {
  root: {
    ...defaultFont,
    fontWeight: "400",
    fontSize: "14px",
    lineHeight: "1.42857",
    letterSpacing: "unset"
  },
  error: {
    '& span': {
      color: dangerColor[0] + " !important",
    },
    '& span svg': {
      color: dangerColor[0] + " !important",
    }
  },
  control: {
    width: '100%',
    display: 'inline-block !important'
  }
};

export default CheckBoxStyle;