import React, {Component} from 'react';
import {Field, reduxForm} from 'redux-form';
import {connect} from 'react-redux';
import {withStyles} from '@material-ui/core/styles/index';
import {MenuItem} from '@material-ui/core';
import SelectFieldInput from '../components/ReduxForms/SelectFieldInput';
import TextFieldInput from '../components/ReduxForms/TextFieldInput';
import ActionForm from '../components/ActionForm';
import {GridContainer, GridItem} from '../components/Grid';
import Muted from '../components/Typography/Muted';
import Center from '../components/Typography/Center';
import validate from "../utils/validation";

const styles = () => ({
  root: {
    maxWidth: '1024px',
    minWidth: '260px',
    height: 'auto',
    margin: '0 auto',
    padding: '20px',
    backgroundColor: 'transparent'
  },
  container: {
    border: '1px solid #fff',
    borderRadius: '5px',
    padding: '20px',
    backgroundColor: '#fff',
  },
  title: {
    color: '#B7C503',
    fontWeight: 500,
    fontSize: 16,
    textAlign: 'left'
  },
  description: {
    color: '#C3C7CF',
    fontWeight: 300,
    fontSize: 14,
    textAlign: 'left'
  },
  link: {
    color: '#25A5DC',
  },
  rightText: {
    textAlign: 'right',
    fontWeight: 'bold',
  }
});

class RequestInfo extends Component {

  render() {
    const {classes, users, handleSubmit} = this.props;
    return(
        <form className={classes.root} onSubmit={handleSubmit}>
          <GridContainer>
            <GridItem>
              <Center>
                <p style={{fontSize: 16, fontWeight: 500}}>{"Fill in your information to continue"}</p>
              </Center>
            </GridItem>
          </GridContainer>
          <div style={{paddingLeft: '8px', paddingRight: '8px'}}>
            <GridContainer className={classes.container}>
              <GridItem xs={6}>
                <label>Full Name</label>
              </GridItem>
              <GridItem className={classes.rightText} xs={6}>
                <label>{users.last_name} {users.first_name}</label>
              </GridItem>
              <GridItem xs={6}>
                <label>Gender</label>
              </GridItem>
              <GridItem className={classes.rightText} xs={6}>
                <label>{users.gender}</label>
              </GridItem>
              <GridItem xs={6}>
                <label>Nationality</label>
              </GridItem>
              <GridItem className={classes.rightText} xs={6}>
                <label>{users.nationality}</label>
              </GridItem>
              <GridItem xs={6}>
                <label>Date of Birth</label>
              </GridItem>
              <GridItem className={classes.rightText} xs={6}>
                <label>{users.dob}</label>
              </GridItem>
            </GridContainer>
          </div>

          <GridContainer>
            <GridItem>
              <Muted>
                <i>{"*If you information isn't correct, please contact our call center."}</i>
              </Muted>
            </GridItem>
            <GridItem>
              <Center space>
                <b>{"Additional Information"}</b>
              </Center>
            </GridItem>
          </GridContainer>

          <GridContainer>
            <GridItem>
              <Field
                  id='occupation'
                  name='occupation'
                  component={SelectFieldInput}
                  label='Occupation'
                  variant='outlined'
              >
                <MenuItem component='li' value={10}>Ten</MenuItem>
                <MenuItem component='li' value={20}>Twenty</MenuItem>
                <MenuItem component='li' value={30}>Thirty</MenuItem>
              </Field>
            </GridItem>
            <GridItem>
              <Field
                  name='currentAddress'
                  component={TextFieldInput}
                  label='Current address'
                  variant='outlined'
              />
            </GridItem>

            <GridItem>
              <Field
                  name='email'
                  component={TextFieldInput}
                  label='Email'
                  variant='outlined'
              />
            </GridItem>

            <GridItem>
              <Field
                  name='phoneNumber'
                  component={TextFieldInput}
                  label='Phone'
                  variant='outlined'
              />
            </GridItem>
            <GridItem>
              <ActionForm single btnLabel={'Continue'} {...this.props} />
            </GridItem>
          </GridContainer>
        </form>
    )
  }
}

const RequestInfoForm = reduxForm({
  form: 'redux_form_lending_form',
  destroyOnUnmount: false, // <------ preserve form data
  forceUnregisterOnUnmount: true, // <------ unregister fields on unmount
  validate
})(RequestInfo);

const mapStateToProps = state => {
  console.log(state);
  return {
    firstTimeLoan: state.firstTimeLoan.data,
    users: state.userInfo.data,
    kycUpdated: state.kycUpdated.data
  };
};

export default connect(mapStateToProps)(withStyles(styles)(RequestInfoForm));