export const doInit = {
  loading: false,
  data: [],
  error: null
};

export const doStart = (state) => ({
  ...state,
  loading: true
});

export const doSuccess = (state, action) => ({
  ...state,
  loading: false,
  error: null,
  data: action.payload.data
});

export const doFail = (state, action) => ({
  ...state,
  loading: false,
  error: action.payload.error
});

export const actionStarted = (actionType) => ({
  type: actionType
});

export const actionSucceeded = (actionType, data) => ({
  type: actionType,
  payload: {
    ...data
  }
});

export const actionFailed = (actionType, error) => ({
  type: actionType,
  payload: {
    error
  }
});