import React, {Component} from 'react';
import {Field, reduxForm} from 'redux-form';
import {connect} from 'react-redux';
import {withStyles} from '@material-ui/core/styles/index';
import {MenuItem, Radio} from '@material-ui/core';
import SelectFieldInput from '../components/ReduxForms/SelectFieldInput';
import TextFieldInput from '../components/ReduxForms/TextFieldInput';
import RadioButtonFieldInput from '../components/ReduxForms/RadioButtonFieldInput';
import ActionForm from '../components/ActionForm';
import {GridContainer, GridItem} from '../components/Grid';
import Center from "../components/Typography/Center";
import validate from "../utils/validation";

const styles = () => ({
  root: {
    maxWidth: '1024px',
    minWidth: '260px',
    height: 'auto',
    margin: '0 auto',
    padding: '20px',
    backgroundColor: 'transparent'
  },
  container: {
    border: '1px solid #fff',
    borderRadius: '5px',
    padding: '20px',
    backgroundColor: '#fff',
  },
  title: {
    color: '#B7C503',
    fontWeight: 500,
    fontSize: 16,
    textAlign: 'left'
  },
  description: {
    color: '#C3C7CF',
    fontWeight: 300,
    fontSize: 14,
    textAlign: 'left'
  },
  link: {
    color: '#25A5DC',
  }
});

class LendingForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currency: "KHR",
      currencyLabel: "៛",
      isMerchant: "N"
    };
    this.handleCurrency = this.handleCurrency.bind(this);
    this.handleMerchant = this.handleMerchant.bind(this);
  }

  handleCurrency = (value) => {
    if(value === "USD") {
      this.setState({currency: value, currencyLabel: "$"});
    } else {
      this.setState({currency: value, currencyLabel: "៛"});
    }
  };

  handleMerchant = (value) => {
    this.setState({isMerchant: value});
  };

  render() {
    const {classes, handleSubmit} = this.props;
    const {currencyLabel, isMerchant} = this.state;
    return(
        <form className={classes.root} onSubmit={handleSubmit}>
          <GridContainer>
            <GridItem>
              <Center>
                <p style={{fontSize: 16, fontWeight: 500}}>{"Fill in your loan request form"}</p>
              </Center>
            </GridItem>
          </GridContainer>
          <GridContainer >
            <GridItem>
              <Field
                  id='branchProvince'
                  name='branchProvince'
                  component={SelectFieldInput}
                  label='Province of Amret Branch'
                  variant='outlined'
              >
                <MenuItem component='li' value={10}>Ten</MenuItem>
                <MenuItem component='li' value={20}>Twenty</MenuItem>
                <MenuItem component='li' value={30}>Thirty</MenuItem>
              </Field>
            </GridItem>
            <GridItem>
              <Field
                  id='branchName'
                  name='branchName'
                  component={SelectFieldInput}
                  label="Amret Branch's Name"
                  variant='outlined'
              >
                <MenuItem component='li' value={10}>Ten</MenuItem>
                <MenuItem component='li' value={20}>Twenty</MenuItem>
                <MenuItem component='li' value={30}>Thirty</MenuItem>
              </Field>
            </GridItem>
            <GridItem>
              <Field
                  id='loanType'
                  name='loanType'
                  component={SelectFieldInput}
                  label="Loan Type"
                  variant='outlined'
              >
                <MenuItem component='li' value={10}>Ten</MenuItem>
                <MenuItem component='li' value={20}>Twenty</MenuItem>
                <MenuItem component='li' value={30}>Thirty</MenuItem>
              </Field>
            </GridItem>
            <GridItem>
              <Field
                  id='loanPurpose'
                  name='loanPurpose'
                  component={SelectFieldInput}
                  label="Loan Purpose"
                  variant='outlined'
              >
                <MenuItem component='li' value={10}>Ten</MenuItem>
                <MenuItem component='li' value={20}>Twenty</MenuItem>
                <MenuItem component='li' value={30}>Thirty</MenuItem>
              </Field>
            </GridItem>

            <GridItem xs={4} style={{fontSize: '13px', lineHeight: '52px'}}>Currency</GridItem>
            <GridItem xs={8} style={{textAlign: 'right'}}>
              <Field
                  name='currency'
                  component={RadioButtonFieldInput}
                  values={[{value: 'KHR', key: 'KHR'}, {value: 'USD', key: 'USD'}]}
                  onChange={(evt) => this.handleCurrency(evt.target.value)}
              >
                <Radio value='KHR' label='KHR'/>
                <Radio value='USD' label='USD'/>
              </Field>
            </GridItem>
            <GridItem xs={4} style={{fontSize: '13px', lineHeight: '52px'}}>Loan Amount</GridItem>
            <GridItem xs={8}>
              <Field
                  type={'number'}
                  name='loanAmount'
                  component={TextFieldInput}
                  label='0.00'
                  variant='outlined'
                  noLabel
                  iconLeft
                  icon={<b style={{fontSize: '20px'}}>{currencyLabel}</b>}
              />
            </GridItem>

            <GridItem>
              <label>Are you Wing's merchant or agent?</label>
            </GridItem>
            <GridItem>
              <Field
                  name='isMerchant'
                  component={RadioButtonFieldInput}
                  values={[{value: 'Y', key: 'Yes'}, {value: 'N', key: 'No'}]}
                  onChange={(evt) => this.handleMerchant(evt.target.value)}
              >
                <Radio value='Y' label='Yes'/>
                <Radio value='N' label='No'/>
              </Field>
            </GridItem>
            {isMerchant==="Y" ?
                <GridItem>
                  <Field
                      name='merchantAccount'
                      component={TextFieldInput}
                      label='Merchant account'
                      variant='outlined'
                  />
                </GridItem>
                : null
            }
            <GridItem>
              <ActionForm single btnLabel={'Apply'} {...this.props} />
            </GridItem>
          </GridContainer>
        </form>
    )
  }
}

const LendingFormForm = reduxForm({
  form: 'redux_form_lending_form',
  destroyOnUnmount: false, // <------ preserve form data
  forceUnregisterOnUnmount: true, // <------ unregister fields on unmount
  validate
})(LendingForm);

const mapStateToProps = state => {
  return {
    initialValues: {
      currency: 'KHR',
      isMerchant: 'N'
    },
    firstTimeLoan: state.firstTimeLoan.data,
    users: state.userInfo.data,
    kycUpdated: state.kycUpdated.data
  };
};

export default connect(mapStateToProps)(withStyles(styles)(LendingFormForm));