import React from "react";
import PropTypes from "prop-types/prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import typographyStyle from '../../assets/jss/material-dashboard-react/components/typographyStyle';

function Center({ ...props }) {
    const { classes, space, children } = props;
    const myClassName = space ? classes.defaultFontStyle + " " + classes.defaultHeaderMargins + " " + classes.centerText : classes.defaultFontStyle + " " + classes.centerText;
    return (
        <div className={myClassName}>
            {children}
        </div>
    );
}

Center.propTypes = {
    classes: PropTypes.object.isRequired
};

export default withStyles(typographyStyle)(Center);
