import React from 'react';
import PropTypes from 'prop-types/prop-types';
import classNames from 'classnames';
import withStyles from '@material-ui/core/styles/withStyles';
import {FormControl, FormControlLabel, Checkbox, FormHelperText} from '@material-ui/core';
import {GridContainer, GridItem} from '../Grid';
import styles from '../../assets/jss/material-dashboard-react/components/CheckBoxStyle';

const GroupCheckBoxFieldInput = ({classes,
                                   className,
                                   disabled,
                                   options,
                                   input,
                                   meta: {touched, invalid, error}}) => {

  const root = classNames({
    [classes.root]: true,
    [classes.error]: touched && invalid && !input.value,
    [className]: className !== undefined
  });

  return (
      <FormControl component='fieldset' className={classes.control}>
        <GridContainer spacing={2}>
          {options.map((option, index) => (
              <GridItem key={index} xs={12} md={3}>
                <FormControlLabel
                    className={root}
                    control={
                      <Checkbox
                          disabled={disabled}
                          value={option.id}
                          checked={input.value.indexOf(option.id) !== -1}
                          onChange={(event) => {
                            const newValue = [...input.value];
                            if (event.target.checked) {
                              newValue.push(option.id);
                            } else {
                              newValue.splice(newValue.indexOf(option.id), 1);
                            }
                            if (newValue.length === 0) {
                              return input.onChange();
                            } else {
                              return input.onChange(newValue);
                            }

                          }}
                          color='primary'
                      />
                    }
                    label={option.name}
                />
              </GridItem>
          ))}
        </GridContainer>
        {!(invalid && error) ? null : <FormHelperText error>{touched && error}</FormHelperText>}
      </FormControl>

  )
};

GroupCheckBoxFieldInput.propTypes = {
  classes: PropTypes.object,
  className: PropTypes.object,
  disabled: PropTypes.bool,
  options: PropTypes.array,
  input: PropTypes.object,
};

export default withStyles(styles)(GroupCheckBoxFieldInput);