import axios from "axios";
import {getToken} from "../../utils/LocalStorage";
import {defaultHeaders} from "../../utils/properties";

export function getAction(url, cancelToken) {
    return axios.get(
        url,
        {
            headers: defaultHeaders(getToken()),
            cancelToken: cancelToken
        });
}

export function postAction(url, body, cancelToken) {
    return axios.post(
        url,
        body,
        {
            headers: defaultHeaders(getToken()),
            cancelToken: cancelToken
        });
}