import axios from "axios";
import qs from 'querystring';
import {credential} from "./properties";
import {addSession} from "./LocalStorage"

export function getAccessToken(cancelToken) {
    console.log("I am here token");
    axios.post("/token",
        qs.stringify(credential),
        {
            headers: {
                "Content-Type": "application/x-www-form-urlencoded"
            },
            cancelToken: cancelToken
        }).then((res) => {
            console.log(res);
        addSession("TOKEN", res.data.access_token);
    }).catch((err) => {
        console.log(err)
    });
}

