import React from 'react';
import PropTypes from 'prop-types/prop-types';
import withStyles from '@material-ui/core/styles/withStyles';
import classNames from 'classnames';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import InputAdornment from '@material-ui/core/InputAdornment';
import TextFieldStyle from '../../assets/jss/material-dashboard-react/components/TextFieldStyle';

const TextFieldInput = ({
                            classes,
                            className,
                            type,
                            disabled,
                            label,
                            noLabel,
                            input,
                            variant,
                            margin,
                            multiline,
                            rows,
                            icon,
                            iconLeft,
                            meta: {touched, invalid, error},
                            ...custom
                        }) => {
    const root = classNames({
        [classes.root]: true,
        [classes.success]: !invalid && !error,
        [className]: className !== undefined
    });

    return (
        <TextField
            className={root}
            disabled={disabled}
            type={type}
            variant={variant}
            margin={margin}
            fullWidth
            label={!noLabel ? label : null}
            placeholder={label}
            multiline={multiline}
            rowsMax={8}
            rows={rows}
            error={touched && invalid}
            helperText={touched && error}
            {...input}
            {...custom}
            InputProps={icon ? iconLeft ? {
                startAdornment: (
                    <InputAdornment position='start'>
                        {icon}
                    </InputAdornment>
                )
            } : {
                endAdornment: (
                    <InputAdornment position='end'>
                        <IconButton
                            edge='end'
                            aria-label='icon'
                        >
                            {icon}
                        </IconButton>
                    </InputAdornment>
                )
            } : null}
        />
    )
};

TextFieldInput.defaultProps = {
    type: 'text',
    iconLeft: false,
    noLabel: false
};

TextFieldInput.propTypes = {
    classes: PropTypes.object,
    className: PropTypes.object,
    type: PropTypes.string,
    disabled: PropTypes.bool,
    label: PropTypes.string,
    noLabel: PropTypes.bool,
    input: PropTypes.object,
    variant: PropTypes.oneOf(['standard', 'outlined', 'filled']),
    icon: PropTypes.node,
    iconLeft: PropTypes.bool,
    multiline: PropTypes.bool,
    rows: PropTypes.number,
    margin: PropTypes.oneOf(['dense', 'normal'])
};

export default withStyles(TextFieldStyle)(TextFieldInput);