import {getAction, postAction} from "./ApiActions";
import {actionStarted, actionSucceeded, actionFailed} from '../initialization/constant';
import {FETCH_USER_INFO, CHECK_KYC_UPDATED} from '../../reducers/actions/type';
import {pathGetUserInfo, pathCheckKYCUpdate} from "../../utils/Constant";

export const getExecuted = (types, url, cancelToken) => {
    return dispatch => {
        dispatch(actionStarted(types[0]));
        getAction(url, cancelToken.token).then(response => {
            console.log("ddd" + response);
            //cancel token
            //cancelToken.cancel("Api is being canceled");
            dispatch(actionSucceeded(types[1], response));
        })
            .catch(error => {
                console.log("Error");
                console.log(error);
                dispatch(actionFailed(types[2], error.message));
            });
    };
};

export const postExecuted = (types, url, data, cancelToken) => {
    console.log(data);
    return dispatch => {
        dispatch(actionStarted(types[0]));
        postAction(url, data, cancelToken).then(response => {
            dispatch(actionSucceeded(types[1], response));
        })
            .catch(error => {
                dispatch(actionFailed(types[2], error));
            });
    };
};

export const checkFirstTimeLoanFlow = (types, url, data, cancelToken) => {
    console.log("First Time Loan");
    return dispatch => {
        dispatch(actionStarted(types[0]));
        postAction(url, data, cancelToken)
            .then(response => {
                if (response.data !== undefined && response.data !== null) {
                    console.log(response.data);
                    if (response.data.first_time === "Yes") {
                        // post action get user info
                        postAction(pathGetUserInfo, data, cancelToken).then(response => {
                            console.log("===============================>");
                            console.log(response.data);
                            dispatch(actionSucceeded(FETCH_USER_INFO[1], response));
                        }).catch(error => {
                            dispatch(actionFailed(FETCH_USER_INFO[2], error));
                        });
                    } else {
                        // post action check kyc updated
                        postAction(pathCheckKYCUpdate, data, cancelToken).then(response => {
                            dispatch(actionSucceeded(CHECK_KYC_UPDATED[1], response));
                        }).catch(error => {
                            dispatch(actionFailed(CHECK_KYC_UPDATED[2], error));
                        });
                    }
                }
                dispatch(actionSucceeded(types[1], response));
            }).catch(error => {
            dispatch(actionFailed(types[2], error));
        });
    };
};