import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {GridContainer, GridItem, Button} from './index';

const useStyles = makeStyles({
  reset: {
    textAlign: 'right',
  },
  submit: {
    textAlign: 'left',
  },
  single: {
    textAlign: 'center',
  }
});

export default function ActionForm(props) {
  const classes = useStyles();
  const {single, btnLabel, pristine, submitting, reset} = props;
  return single ?
      <GridContainer>
        <GridItem className={classes.single}>
          <Button type='submit' color='primary' disabled={pristine || submitting}>{btnLabel}</Button>
        </GridItem>
      </GridContainer>
      :
      <GridContainer>
        <GridItem className={classes.reset} xs={6}>
          <Button color='warning' onClick={reset}>Reset</Button>
        </GridItem>
        <GridItem xs={6}>
          <Button type='submit' color='primary' disabled={pristine || submitting}>{btnLabel}</Button>
        </GridItem>
      </GridContainer>
}

ActionForm.defaultProps = {
  btnLabel: 'Submit'
};