import React from 'react';
import PropTypes from 'prop-types/prop-types';
import classNames from "classnames";
import withStyles from "@material-ui/core/styles/withStyles";
import {
  FormControl,
  FormControlLabel,
  Checkbox,
  FormHelperText
} from '@material-ui/core';
import styles
  from '../../assets/jss/material-dashboard-react/components/CheckBoxStyle';

const CheckBoxFieldInput = ({...props}) => {
  const {
    classes,
    className,
    label,
    input,
    meta: {touched, invalid, error}
  } = props;

  const root = classNames({
    [classes.root]: true,
    [classes.error]: touched && invalid && !input.value,
    [className]: className !== undefined
  });
  return (
      <FormControl component="fieldset">
        <FormControlLabel
            className={root}
            control={
              <Checkbox
                  checked={!!input.value}
                  onChange={input.onChange}
                  value={!input.value}
                  color="primary"
              />
            }
            label={label}
        />
        {!(invalid && error) ? null : <FormHelperText error>{touched
        && error}</FormHelperText>}
      </FormControl>
  )
};

CheckBoxFieldInput.propTypes = {
  classes: PropTypes.object,
  className: PropTypes.string,
  label: PropTypes.node,
  input: PropTypes.object,
};

export default withStyles(styles)(CheckBoxFieldInput);