import React from 'react';
import PropTypes from 'prop-types/prop-types';
import withStyles from '@material-ui/core/styles/withStyles';
import classNames from 'classnames';
import {FormControl, InputLabel, Select, FormHelperText} from '@material-ui/core';
import TextFieldStyle from '../../assets/jss/material-dashboard-react/components/TextFieldStyle';

const SelectFieldInput = ({
                            classes,
                            className,
                            id,
                            input,
                            label,
                            variant,
                            margin,
                            meta: {touched, invalid, error},
                            children,
                            ...custom
                          }) => {
  const inputLabel = React.useRef(null);
  const [labelWidth, setLabelWidth] = React.useState(0);
  React.useEffect(() => {
    setLabelWidth(inputLabel.current.offsetWidth);
  }, [inputLabel]);

  const root = classNames({
    [classes.root]: true,
    [classes.success]: !invalid && !error,
    [className]: className !== undefined
  });

  return (
      <FormControl className={root} margin={margin} fullWidth component="div" variant={variant}
                   error={touched && invalid}>
        <InputLabel ref={inputLabel} htmlFor={id}>{label}</InputLabel>
        <Select
            labelWidth={labelWidth}
            {...input}
            {...custom}
            inputProps={{
              id: {id}
            }}
        >
          {children}
        </Select>
        {!(invalid && error) ? null : <FormHelperText error>{touched && error}</FormHelperText>}
      </FormControl>
  )
};


SelectFieldInput.propTypes = {
  classes: PropTypes.object,
  className: PropTypes.object,
  id: PropTypes.string.isRequired,
  label: PropTypes.string,
  input: PropTypes.object,
  variant: PropTypes.oneOf(['standard','outlined','filled']),
  children: PropTypes.node.isRequired,
  margin: PropTypes.oneOf(['dense','normal'])
};

export default withStyles(TextFieldStyle)(SelectFieldInput);