import axios from "axios";

const requestPayment = (amount, productName) => {

    let userAgent = navigator.userAgent;
    // alert(JSON.stringify(userAgent));
    let wingAccount = '';
    if (userAgent !== undefined && userAgent !== null && userAgent !== '') {
        wingAccount = userAgent.split(':')[4];
    }

    let ref = Math.random().toString(36).substr(2, 16);
    let data = {
        partner_id: "BOOKMEBUS",
        api_key: "391945f1cd86cc0956c8605e12a78ba390c090c58251e9c8916ec226c16e4705",
        partner_ref_number: ref,
        amount: amount,
        currency: "USD",
        sandbox: "1",
        product_detail: {
            product_id: "PRO1221",
            product_type: "TYPE_HEALTH",
            product_name: productName,
            product_qty: 3,
            product_unit_price: 4,
            transaction_type: "REQUEST"
        }
    };

    axios.post("https://stageonline.wingmoney.com/wingonlinesdk/api/v1/h5payment/preorder",
        data,
        {
            headers: {
                "content-type": "application/json",
                "cache-control": "no-cache",
            }
        })
        .then(response => {
            if (response.status_code === '200') {
                if (response.result !== '' && response.result !== 'undefined') {
                    const confData = {
                        partnerId: response.result.partnerId,
                        orderId: response.result.orderId,
                        partnerRefNumber: response.result.partnerRefNumber,
                        amount: response.result.amount,
                        currency: response.result.currency
                        // wingAccount: wingAccount
                    };
                    // alert(JSON.stringify(confData));
                    WingPayBridge.invoke(JSON.stringify(confData));
                } else {
                    // alert('Request Pre Order fail');
                    console.log("Testing");
                }
            }
        })
        .catch(error => {
            console.log(error)
        });


    const WingPayBridge = function () {
        return {
            init: function () {
            },
            invoke: function (reqData) {
                // get userAgent from request
                const userAgent = navigator.userAgent;
                // check if request from iOS or Android device
                if (userAgent.includes("iOS")) {
                    window.webkit.messageHandlers.wingPay.postMessage(reqData);
                } else {
                    // eslint-disable-next-line no-undef
                    WingPay.invoke(reqData);
                }
                // WingPay.invoke(reqData);
            },
            callback: function (respData) {
                axios.get("https://stageonline.wingmoney.com/wingh5sample/callback?data=" + respData, {
                    headers: {
                        "content-type": "application/json",
                        "cache-control": "no-cache"
                    },
                    data: JSON.stringify(respData)
                }).then(response => {
                    if (response.status_code === '200') {
                        if (response.result !== '' && response.result !== 'undefined') {
                            alert('Success');
                            console.log("Success");
                        } else {
                            alert('Fail');
                            console.log("Fail");
                        }
                    }
                })
                    .catch(error => {
                        alert('Fail');
                        console.log(error);
                    });
            }
        }
    }();
};

export default requestPayment;