import {combineReducers} from 'redux';
import { reducer as formReducer } from 'redux-form';
import {checkKYCStatus, checkFirstTimeLoan, checkKYCUpdated, fetchUserInfo, verifyMerchantInfo} from './initialization/index';

export default combineReducers({
    form: formReducer,
    kycStatus: checkKYCStatus,
    firstTimeLoan: checkFirstTimeLoan,
    kycUpdated: checkKYCUpdated,
    userInfo: fetchUserInfo,
    merchantInfo: verifyMerchantInfo
});
