import React from "react";
import {withStyles} from '@material-ui/core/styles/index';

const styles = () => ({
  root: {
    width: "100%",
    paddingTop: "50px",
    backgroundColor: "white",
    textAlign: "center",
    overflow: "hidden",
    "& div:first-child": {
      height: "110px",
      backgroundPosition: "center"
    },
    "& div h1": {
      fontSize: "80px"
    },
    "& div:nth-child[2]": {
      marginTop: "-50px"
    },
    "& div:nth-child[2] h3": {
      fontSize: "80px"
    },
  },
  link: {
    color: "#fff!important",
    padding: "10px 20px",
    backgroundColor: "green",
    margin: "20px 0",
    display: "inline-block",
    textDecoration: "none"
  },
});

class NotFound extends React.Component {

  render() {
    const {classes} = this.props;
    return(
        <div className={classes.root}>
          <div>
            <h1>404</h1>
          </div>
          <div>
            <h3>
              That's an error
            </h3>
            <p>You're trying to request invalid URL, That's all we know.</p>
            <a href="/" className={classes.link}>Go to Home</a>
          </div>
        </div>
    )
  }
}

export default withStyles(styles)(NotFound);