const validate = values => {
    const errors = {};
    const requiredFields = [
        'termCondition',
        'occupation',
        'currentAddress',
        'email',
        'phoneNumber',
        'branchProvince',
        'branchName',
        'loanType',
        'loanPurpose',
        'loanAmount',
        'merchantAccount'
    ];
    requiredFields.forEach(field => {
        if (!values[field]) {
            errors[field] = 'Required';
        }
        if(field === "email" && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values[field])) {
            errors[field] = 'Invalid email format';
        }
        if(field === "loanAmount" && !/^[+]?([.]\d+|\d+([.]\d+)?)$/i.test(values[field])) {
            errors[field] = 'Invalid amount format';
        }
    });
    return errors;
};

export default validate;