import {doInit, doStart, doSuccess, doFail} from './constant';
import {
  CHECK_KYC_STATUS,
  CHECK_FIRST_TIME_LOAN,
  CHECK_KYC_UPDATED,
  FETCH_USER_INFO,
  VERIFY_MERCHANT_INFO} from '../actions/type';

function checkKYCStatus(state = doInit, action) {
  switch (action.type) {
    case CHECK_KYC_STATUS[0]:
      return doStart(state);
    case CHECK_KYC_STATUS[1]:
      return doSuccess(state, action);
    case CHECK_KYC_STATUS[2]:
      return doFail(state, action);
    default:
      return state;
  }
}

