import React, {Component} from 'react';
import axios from "axios";
import {connect} from 'react-redux';
import {withStyles} from '@material-ui/core/styles/index';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import IconButton from '@material-ui/core/IconButton';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import NonFullKYC from './NonFullKYC';
import {getAccessToken, getTest} from '../utils/GrantAccess';
import {postExecuted} from '../reducers/actions/action';
import {CHECK_KYC_STATUS} from '../reducers/actions/type';
import {pathCheckKYCStatus} from "../utils/Constant";
import Lending from "./Lending";

const styles = () => ({
    root: {
        maxWidth: '1024px',
        minWidth: '260px',
        height: 'auto',
        margin: '0 auto',
        padding: '20px',
        backgroundColor: 'transparent'
    },
    container: {
        backgroundColor: '#fff',
    },
    inline: {
        display: 'inline',
        color: '#B7C503',
    },
});
const cancelToken = axios.CancelToken.source();

class AvailableLender extends Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            lending: false
        };

        this.handleClick = this.handleClick.bind(this);
        this.handleClose = this.handleClose.bind(this);
    }

    componentDidMount() {
        getAccessToken(cancelToken.token);
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        console.log(nextProps);
        if (nextProps.kycStatus !== undefined && nextProps.kycStatus.full_kyc !== undefined) {
            console.log("I'm on stage" + nextProps.kycStatus.full_kyc);
            if (nextProps.kycStatus.full_kyc === "Yes") {
                console.log(nextProps.kycStatus);
                return ({open: false, lending: true});
            } else {
                return ({open: true, lending: false});
            }
        }
        return ({open: prevState.open, lending: prevState.lending});
    }

    componentWillUnmount() {
        cancelToken.cancel("Api is being canceled");
    }

    handleClick = () => {
        this.props.onCheckKYCStatus(pathCheckKYCStatus, {account_number: "01044000"}, cancelToken.token);
    };

    handleClose = () => {
        this.setState({open: false});
        this.props.history.push('/lending');
    };

    render() {
        const {classes} = this.props;
        if (this.state.lending) {
            return <Lending />
        } else {
            return (
                <div className={classes.root}>
                    <div className={classes.container}>
                        <List>
                            <ListItem alignItems="flex-start" button onClick={this.handleClick}>
                                <ListItemAvatar>
                                    <Avatar alt="Amret" src="/static/images/avatar/1.jpg"/>
                                </ListItemAvatar>
                                <ListItemText
                                    primary="Amret"
                                    secondary={
                                        <React.Fragment>
                                            <Typography
                                                component="span"
                                                variant="body2"
                                                className={classes.inline}
                                            >
                                                Easy, Flexible and Convenience
                                            </Typography>
                                        </React.Fragment>
                                    }
                                />
                                <ListItemSecondaryAction>
                                    <IconButton edge="end" aria-label="comments">
                                        <KeyboardArrowRight style={{fontSize: '42px', color: '#B7C503'}}/>
                                    </IconButton>
                                </ListItemSecondaryAction>
                            </ListItem>
                        </List>
                    </div>
                    <NonFullKYC open={this.state.open} handleClose={this.handleClose}/>
                </div>
            )
        }

    }
}

const mapStateToProps = state => {
    return {
        kycStatus: state.kycStatus.data
    };
};

const mapDispatchToProps = dispatch => {
    return {
        onCheckKYCStatus: (url, data, cancelToken) => {
            dispatch(postExecuted(CHECK_KYC_STATUS, url, data, cancelToken));
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(AvailableLender));