import React, {Component} from 'react';
import {Field, reduxForm} from 'redux-form';
import {Link} from 'react-router-dom';
import {withStyles} from '@material-ui/core/styles/index';
import {Icon, MenuItem} from '@material-ui/core';
import SelectFieldInput from '../components/ReduxForms/SelectFieldInput';
import TextFieldInput from '../components/ReduxForms/TextFieldInput';
import ActionForm from '../components/ActionForm';
import Backspace from '@material-ui/icons/Backspace'
import {GridContainer, GridItem} from '../components/Grid';
import Button from "../components/Buttons/Button.jsx";
import MyIcon from '../assets/img/favicon.png';

const styles = () => ({
  root: {
    maxWidth: '1024px',
    minWidth: '260px',
    height: 'auto',
    margin: '0 auto'
  },
  container: {
    border: '1px solid #fff',
    borderRadius: '5px',
    padding: '25px 20px 15px 20px',
    backgroundColor: '#fff',
    textAlign: 'center',
  },
  pin: {
    width: '20px',
    height: '15px',
    padding: '20px 5px 20px 5px',
    margin: '0 auto',
    border: '1px solid #000',
    borderRadius: '10px',
    backgroundColor: '#F1F2F3',
  },
  dot: {
    width: '15px',
    height: '15px',
    borderRadius: '15px',
    margin: '0 auto',
    backgroundColor: '#000'
  }
});

class PIN extends Component {

  render() {
    const {classes, handleSubmit} = this.props;
    return (
        <form className={classes.root} onSubmit={handleSubmit}>
          <div className={classes.container}>
            <GridContainer>
              <GridItem style={{textAlign: 'center'}}>
                <img src={MyIcon} alt={'Confirmation'}/>
                <h5>Please input your PIN code</h5>
              </GridItem>
            </GridContainer>
            <GridContainer style={{paddingTop: '10px', paddingBottom: '15px'}}>
              <GridItem xs={2}> </GridItem>
              {[1, 2, 3, 4].map((value, i) => {
                return(
                    <GridItem xs={2} key={i}>
                      <div className={classes.pin}>
                        <div hidden={false} className={classes.dot}> </div>
                      </div>
                    </GridItem>
                )
              })}
            </GridContainer>
            <GridContainer>
              {[1, 2, 3, 4, 5, 6, 7, 8, 9, 'C', 0, <Backspace/>].map(
                  (value, i) => {
                    return (
                        <GridItem xs={4}>
                          <Button color='warning' size='lg' justIcon={i < 11}
                                  round={i < 11}>{value}</Button>
                        </GridItem>
                    )
                  })}
            </GridContainer>
          </div>
        </form>
    )
  }
}

const PINForm = reduxForm({
  form: 'redux_form_confirmation',
})(PIN);

export default withStyles(styles)(PINForm);