import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types/prop-types';
import {FormControl, FormControlLabel, RadioGroup, Radio} from '@material-ui/core';

const useStyles = makeStyles({
    root: {
        '&:hover': {
            backgroundColor: 'transparent',
        },
    },
    icon: {
        borderRadius: '50%',
        width: 20,
        height: 20,
        boxShadow: 'inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1)',
        backgroundColor: '#B7C503',
        backgroundImage: 'linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0))',
        '$root.Mui-focusVisible &': {
            outline: '2px auto rgba(19,124,189,.6)',
            outlineOffset: 2,
        },
        'input:hover ~ &': {
            backgroundColor: '#B7C503',
        },
        'input:disabled ~ &': {
            boxShadow: 'none',
            background: 'rgba(206,217,224,.5)',
        },
    },
    checkedIcon: {
        backgroundColor: '#B7C503',
        backgroundImage: 'linear-gradient(180deg,hsla(0,0%,100%,.1),hsla(0,0%,100%,0))',
        '&:before': {
            display: 'block',
            width: 20,
            height: 20,
            backgroundImage: 'radial-gradient(#fff,#fff 28%,transparent 32%)',
            content: '""',
        },
        'input:hover ~ &': {
            backgroundColor: '#B7C503',
        },
    },
});


const RadioButtonFieldInput = ({...props}) => {
    const classes = useStyles();
  const {
    values,
    input,
    rest
  } = props;

  return (
      <FormControl component='fieldset'>
        <RadioGroup {...input} {...rest} style={{display: 'inline-block'}}>
          {values.map((data, key) => {
            return (
                <FormControlLabel key={key} value={data.value} control={<Radio color='primary' checkedIcon={<span className={clsx(classes.icon, classes.checkedIcon)} />}/>} label={data.key}/>
            )
          })}
        </RadioGroup>
      </FormControl>
  )
};

RadioButtonFieldInput.propTypes = {
  values: PropTypes.array,
  input: PropTypes.object,
  rest: PropTypes.object
};

export default RadioButtonFieldInput;